import Vue from 'vue'
import { wrapFunctional } from './utils'

const components = {
  ContentsList: () => import('../../../../components/contentsList.vue' /* webpackChunkName: "components/contents-list" */).then(c => wrapFunctional(c.default || c)),
  WidgetStack: () => import('../../../../components/widget/Stack.vue' /* webpackChunkName: "components/widget-stack" */).then(c => wrapFunctional(c.default || c)),
  WidgetProfile: () => import('../../../../components/widget/profile.vue' /* webpackChunkName: "components/widget-profile" */).then(c => wrapFunctional(c.default || c))
}

for (const name in components) {
  Vue.component(name, components[name])
  Vue.component('Lazy' + name, components[name])
}
